import * as React from "react";
import { Component } from "react";

import Intro from "components/Intro";
import Game from "components/Game";
import Question from "components/Question";

import questions from "utils/questionsList";

interface State {
    isShowingQuestion: boolean;
}

class Index extends Component<{}, State> {
  state = {
    isShowingQuestion: false
  };

  render() {
    return (
      <div className="app">
        <Intro />
        <FightStart toShow={this.props.state === "start"} />
        <Question question={questions[0]} toShow={this.state.isShowingQuestion} />
        <Game />
      </div>
    );
  }
}

export default Index;
